import Vue from 'vue'
import vueRouter from 'vue-router'


import index from '../components/index'
import ivew from '../components/ivew'
import content1 from '../components/content1'
import content2 from '../components/content2'
// import ex from '../components/ex'
Vue.use(vueRouter);


export default new vueRouter({

    routes:[
        {
            path:'/index',component:index,name:'index',

        },
        {
            path:'/ex',
            component:()=>import('../components/ex'),
            name:'ex',



        },
        // {
        //     path:'/content1',
        //     component:content1,
        //     name:'content1',
        //
        //
        //
        // },
        // {
        //     path:'/content2',
        //     component:content2,
        //     name:'content2',
        //
        //
        //
        // },
        {
            path:'/ivew',component: ivew,name:'ivew',
            meta:{
                title:'列表',

            },
            children:[
                {
                    path:'content1',
                    component:content1,
                    name:'content1',
                    meta:{title:'哈哈',id:1,icon:'ios-analytics-outline'}

                },
                {
                    path:'content2',
                    component:content2,
                    name:'content2',
                    meta:{title:'嘿嘿',id:2,icon:'logo-android'}
                },
            ]
        }
    ]
})
