<style scoped>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
        height: 100%;
    }
    .layout-logo{
        width: 100px;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        float: left;
        position: relative;
        top: 15px;
        left: 20px;
    }
    .layout-nav{
        width: 420px;
        margin: 0 auto;
        margin-right: 20px;
    }
    .ivu-layout{
        height: 100%;

    }
    .ivu-layout.ivu-layout-has-sider{
        height: 100%;
    }
</style>
<template>
    <div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <Icon type="ios-navigate"></Icon>
                            Item 1
                        </MenuItem>
                        <MenuItem name="2">
                            <Icon type="ios-keypad"></Icon>
                            Item 2
                        </MenuItem>
                        <MenuItem name="3">
                            <Icon type="ios-analytics"></Icon>
                            Item 3
                        </MenuItem>
                        <MenuItem name="4">
                            <Icon type="ios-paper"></Icon>
                            Item 4
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
            <Layout>
                <!--<list></list>-->

                <Sider hide-trigger :style="{background: '#fff'}">
                    <Menu :active-name="tabid" theme="light" width="auto" :open-names="['1']">
                        <!--<Submenu name="1" v-for="(item,index) in tabArr" :key="index">-->
                            <!--<template slot="title">-->
                                <!--<Icon type="ios-navigate"></Icon>-->
                                <!--{{ item.name }}-->
                            <!--</template>-->
                            <!--<MenuItem :name="i.id" v-for="(i,index2) in item.children" :key="index2"  ><router-link :to="{name:i.name}" ><div @click="conIndex(index2,index)">{{ i.name }}</div></router-link></MenuItem>-->

                        <!--</Submenu>-->
                        <Submenu name="1" >
                            <template slot="title">
                                <Icon type="ios-navigate"></Icon>
                                第一级菜单
                            </template>
                            <MenuItem :name="item.meta.id" v-for="(item,index) in this.$router.options.routes[2].children" :key="index" >
                                <!--<router-link :to="{name:item.path}" style="width: 133px;display: block">-->
                                    <Icon :type="item.meta.icon" />
                                    <div style="display: inline-block;width: 100px" @click="changeContent(index)">{{ item.meta.title }}</div>
                                <!--</router-link>-->
                            </MenuItem>

                        </Submenu>
                    </Menu>
                </Sider>
                <Layout :style="{padding: '0 24px 24px'}">
                    <!--<div style="width: 100px;height: 100px;background: red" @click="toex">125</div>-->
                    <div style="margin: 20px 0" >
                        <template >
                            <!--<Tag type="border" closable color="primary">标签一</Tag>-->
                            <!--<Tag type="border" closable color="success">标签二</Tag>-->
                            <!--<Tag type="border" closable color="error">标签三</Tag>-->
                            <span v-for="(tab,index3) in tabList"  :key="index3" >
                                <Tag type="border" closable color="warning"  @on-close="handleClose(index3)"  checkable @on-change="tabChange(index3)">
                                    {{ tab.name }}
                                </Tag>
                            </span>

                        </template>
                    </div>

                    <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">

                <keep-alive include="content1,content2">
                    <router-view></router-view>
                </keep-alive>

                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
</template>
<script>
    // import list from './list'
    export default {
        data:function () {
            return {
                tabArr:[{id:1,name:'hh',children:[{id:2,name:'content1'},{id:3,name:'content2'}]},
                    {id:5,name:'ppc',hildren:[{id:6,name:'6'},{id:7,name:'7'},{id:8,name:'8'}]}
                ],
                listarr:["hh","content1"],
                arrRouter:[],
                tabList:[],
                tabid:'',
                aliveName:''
            }


        },
        created:function(){

            var arru = [1,3,5,6];
            arru.map(function () {
                window.console.log(this);

            })
            this.arrRouter = this.$router.options.routes[2].children
            // window.console.log()
            // this.axios.get('http://localhost:8081/demo.json').then(res=>{
            //
            //     // console.log(res);
            //     window.console.log(res)
            //     this.$router.push('/index')
            //
            // })
        },
        methods:{
            conIndex:function (index2,index) {
                this.listarr = []
                window.console.log(index2,index);
                this.listarr.push( this.tabArr[index].name)
                this.listarr.push( this.tabArr[index].children[index2].name)

                window.console.log(this.listarr);

                var arru = [1,3,5,6];
                arru.filter((i,index)=>index>3)

                // window.console.log( arru.filter((i,index)=>i>3))
                // window.console.log( arru)

            },
            handleClose (index) {



                if(this.tabList[this.tabList.length-1]){

                    var path = this.tabList[this.tabList.length-1].path;

                    // window.console.log(path)
                    this.$router.push("/ivew/"+path)
                    this.tabid = this.tabList[this.tabList.length-1].id;
                    this.tabList.splice(index,1)

                }

                // window.console.log(this.tabid)
                // window.console.log(this.arrRouter[index].meta.id )
                // this.$router.back(-2)
            },
            changeContent:function (index) {
                this.tabid = this.arrRouter[index].meta.id;
                this.$router.push('/ivew/'+this.arrRouter[index].path)
                this.aliveName = this.arrRouter[index].path
                // window.console.log(this.arrRouter[index].path);
                var res = this.tabList.findIndex(ol=>{
                    return this.arrRouter[index].meta.id == ol.id
                })
                if(res==-1){
                    // window.console.log(res)
                    var obj={};
                    obj.name = this.arrRouter[index].meta.title;
                    obj.id = this.arrRouter[index].meta.id;
                    obj.path = this.arrRouter[index].path;
                    this.tabList.push(obj)
                }
                // window.console.log(this.tabList);


                // this.$router.push("/welcome")

            },
            tabChange:function (index) {

                this.tabid = this.tabList[index].id;
                this.aliveName = this.tabList[index].path;
                window.console.log(this.aliveName)
                // window.console.log(this.tabid)
                this.$router.push('/ivew/'+this.tabList[index].path)
            },


            // toex:function () {
            //     window.console.log("pppp")
            //     this.$router.push("ex")
            // }


        },
        components:{
            // list
        }


    }
</script>
